<template>
  <div style="background: white;margin: auto;">
    <apexchart type=donut width="100%" :height="height" :options="chartOptions" :series="series"/>
  </div>
</template>

<script>
import util from './utils'
export default {
  name: 'Donut',
  props: {
    height: {
      default: 300,
      type: Number
    }
  },
  data () {
    return {
      series: [44, 55, 41, 17, 15],
      chartOptions: {
        labels: ['Team A', 'Team B', 'Team C', 'Team D', 'Team E'],
        colors: util.Color.getColors(),
        responsive: [{
          // breakpoint: 480,
          options: {
            chart: {
              // width: 200
            },
            legend: {
              // position: 'bottom'
            }
          }
        }]
      }
    }
  }
}
</script>

<style scoped>

</style>



