      <a-col :span="8"> <pie></pie></a-col>
      <a-col :span="8"><bar :height="262"></bar></a-col>
    </a-row>
    <a-row :gutter="16" style="margin-bottom: 16px;padding: 0 8px;">
      <d-line></d-line>
    </a-row>
    <a-row :gutter="16" style="margin-bottom: 16px;">
      <a-col :span="8"> <column></column></a-col>
      <a-col :span="16"> <d-area></d-area></a-col>
    </a-row>
  </div>
</template>

<script>
import ACol from 'ant-design-vue/es/grid/Col'
import DArea from './Area'
import Bar from './Bar'
import Column from './Column'
import Donut from './Donut'
import DLine from './Line'
import Pie from './Pie'

export default {
  name: 'Dashboard',
  components: {ACol, DArea, Bar, Column, Donut, DLine, Pie}
}
</script>

