<template>
  <div style="background: white;margin: auto;">
    <apexchart type=pie width="100%" :height="height" :options="chartOptions" :series="series"/>
  </div>
</template>

<script>
import util from './utils'

export default {
  name: 'Pie',
  props: {
    height: {
      default: 300,
      type: Number
    }
  },
  data () {
    return {
      series: [44, 55, 13, 43, 22],
      chartOptions: {
        noData: {
          text: '暂无统计数据'
        },
        theme: {
          // mode: 'dark'
          // palette: 'palette4'
        },
        chart: {
          toolbar: {
            // show: true
          }
        },
        colors: util.Color.getColors(),
        labels: ['Team A', 'Team B', 'Team C', 'Team D', 'Team E'],
        responsive: [{
          // breakpoint: 480,
          options: {
            chart: {
              // width: 200
            },
            legend: {
              position: 'left'
            }
          }
        }]
      }
    }
  }
}
</script>

<style scoped>

</style>



