<template>
  <div style="background: white;">
    <apexchart width="100%" :height="height" type="bar" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>
// histogram
<script>
import util from './utils'

export default {
  name: 'Bar',
  props: {
    height: {
      default: 400,
      type: Number
    }
  },
  data () {
    return {
      series: [{
        data: [64, 22, 43, 21]
      }, {
        data: [52, 13, 44, 32]
      }],
      chartOptions: {
        chart: {
          toolbar: {
            show: false
          }
        },
        legend: {
          show: false
        },
        colors: util.Color.getColors(),
        plotOptions: {
          bar: {
            horizontal: true,
            dataLabels: {
              position: 'top'
            }
          }
        },
        dataLabels: {
          enabled: true,
          offsetX: -6,
          style: {
            fontSize: '12px',
            colors: ['#fff']
          }
        },
        stroke: {
          show: true,
          width: 1,
          colors: ['#fff']
        },

        xaxis: {
          categories: [2004, 2005, 2006, 2007]
        }
      }
    }
  }
}
</script>

<style scoped>

</style>



