<template>
  <div style="background: white;">
    <apexchart width="100%" height=400 type="bar" :options="chartOptions" :series="series"></apexchart>
  </div>
</template>

<script>
import util from './utils'

export default {
  name: 'Histogram_Column',
  data () {
    return {
      chartOptions: {
        chart: {
          // id: 'vuechart-example'
          toolbar: {
            show: false
          }
        },
        colors: util.Color.getColors(),
        xaxis: {
          categories: [1991, 1992, 1993, 1994, 1995, 1996, 1997, 1998]
        }
      },
      series: [{
        name: 'series-1',
        data: [30, 40, 35, 50, 49, 60, 70, 91]
      }, {
        name: 'series-2',
        data: [10, 25, 15, 40, 69, 64, 51, 70]
      }]
    }
  }
}
</script>

<style scoped>

</style>



